import MeetingListComponent from "../component/MeetingListComponent"
import {useEffect,useState} from 'react'
import Button from "../component/Button"
import { useHistory } from "react-router-dom";
import {useSelector} from 'react-redux'
import SearchInput from "../component/SearchInput";
import FilterButton from "../component/FilterButton";
import Modal from "../component/Modal";



function Home() {
    const meetings = useSelector((state)=>state.meetings.data)
    const isLoading = useSelector((state)=>state.meetings.isLoading)

    const [search,setSearch] = useState('')
    const [data,setData] = useState([])
    const [checked,setChecked] = useState([])
  

    const history = useHistory();

    const clickCreate = ()=>{
        history.push("/create");
    }

    const onHandleSearch = (e) => {
        setSearch(e.target.value)
    }

    const handleToggle = (value) => () => {
      const currentIndex = checked.indexOf(value);
      const newChecked = [...checked];
  
      if (currentIndex === -1) {
        newChecked.push(value);
      } else {
        newChecked.splice(currentIndex, 1);
      }
  
      setChecked(newChecked);
    };

    console.log(checked)
 
    useEffect(() => {
      var filteredData;

      if(checked.length === 0) {
        filteredData = meetings
      }
      else {
        filteredData = meetings.filter(list => 
          checked.indexOf(list.status) !== -1
          )
      }
      setData(filteredData)
    }, [checked,meetings])


    // useEffect(()=>{
    //   setData(meetings)
    //   console.log('sample')
    // },[meetings])

    return (
      <div className="flex flex-col h-screen max-w-3xl container mx-auto bg-gray-50 relative">
        <header className="h-auto bg-green-400 shadow p-5 pt-1">
                        <div>
                          <p className='text-3xl mb-2 mt-2 text-white'>Calendar</p>
                        </div>

                        <div className='flex'>    
                          <div className='flex-grow'>            
                            <SearchInput onChange={onHandleSearch} />
                          </div>
                          <div className='flex-none pl-2 p-0'>
                            <FilterButton list={['Pending','On-going','Done']} checked={checked} handleToggle={handleToggle}/>
                          </div>
                        </div>
        </header>

        <main className="mb-auto h-auto  overflow-auto p-2 pt-1 ">   
         {
            isLoading ? <div>loading...</div> 
            :data.filter(meeting => meeting.title.toLowerCase().includes(search.toLowerCase()))
                      .map(filteredMeeting => 
                          <MeetingListComponent meeting={filteredMeeting} key={filteredMeeting.id} />
                        )
          }  
        </main>

        <footer className="h-auto  bg-white shadow-hg p-4">
            <Button label='Create' onClick={clickCreate}/>
        </footer>

  
      </div>
    )
}





export default Home
