import React from 'react'
import Button from '../component/Button'
import { useState } from 'react'
import DatePicker from "react-datepicker";
import { useHistory } from 'react-router';
import "react-datepicker/dist/react-datepicker.css";
import { useDispatch } from "react-redux";
import { bindActionCreators } from 'redux'
import { actionCreators } from "../state/index"
import Textfield from '../component/Textfield';
import DateField from '../component/DateField';
import Dropdown from '../component/Dropdown';



function Create() {
    const history = useHistory()
    const [title, setTitle]  = useState('')
    const [dateMeeting, setDateMeeting]  = useState('')
    const [status, setStatus]  = useState('Pending')

    const dispatch = useDispatch();
    const { postMeeting } = bindActionCreators(actionCreators, dispatch);


    const onSelectedStatus = (e) => {
        setStatus(e.target.value)
    }

    const onClickAdd = ()=>{
        postMeeting(title,status,dateMeeting)
        history.push('/')
    }

    

    console.log(status)

    return (
        <div className="flex flex-col h-screen max-w-3xl container mx-auto bg-gray-50">
             <header className="h-auto bg-green-400 shadow p-5 pt-1">
                <div className='sm: p-2 md:p-6 flow-root'>
                    
                    <button className='float-left text-white p-2' onClick={()=>{history.push('/')}}>
                    <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6 " fill="none" viewBox="0 0 24 24" stroke="currentColor">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M10 19l-7-7m0 0l7-7m-7 7h18" />
                    </svg>
                    </button>
                    
                    <h3 className='text-center text-3xl text-white'>Create</h3>
                </div>
             </header>

             <main className="mb-auto h-auto  overflow-auto p-6 pt-1">   
                <Textfield value={title} onChange={(e)=>{setTitle(e.target.value)}} label='Meeting Title'/>  
                <DateField label='Date' selected={dateMeeting} onChange={(date) => setDateMeeting(date)}  />
                <Dropdown label='Status' value={status} selected = {status} onChange={onSelectedStatus} option={['Pending','On-going','Done']} />      
             </main>
            
            <footer className="h-auto  bg-white shadow-hg p-4">
                <Button label="Add" onClick={onClickAdd} />
            </footer>
        
    
        </div>
        
    )
}

export default Create
